import { ADD_UPLOAD, REMOVE_UPLOAD, SET_UPLOADS } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_UPLOADS: {
            return Object.assign(Object.assign({}, state), { uploads: action.payload });
        }
        case ADD_UPLOAD: {
            return Object.assign(Object.assign({}, state), { uploads: [...state.uploads, action.payload] });
        }
        case REMOVE_UPLOAD: {
            return Object.assign(Object.assign({}, state), { uploads: state.uploads.filter((upload) => upload.id !== action.payload) });
        }
        default: {
            return state;
        }
    }
};
export { reducer };
