import { useContext } from "@wordpress/element";
import { AppContext } from "../providers/AppContextProvider";
import { convertToTimezone, convertUTCDatetimeToWPTimezone, } from "../utils/timezone";
const useTimezone = () => {
    const { state: { timezone }, } = useContext(AppContext);
    const convertToWPTimezone = (utcDateTime) => {
        return convertToTimezone(utcDateTime, timezone);
    };
    const convertUTCDateTimeToWPTimezone = (utcDateTime) => {
        return convertUTCDatetimeToWPTimezone(utcDateTime, timezone);
    };
    return { convertToWPTimezone, convertUTCDateTimeToWPTimezone };
};
export { useTimezone };
