import { ADD_PIPELINE_WEBHOOK, EDIT_PIPELINE_WEBHOOK, REMOVE_PIPELINE_WEBHOOK, SET_PIPELINE_WEBHOOKS, SET_PIPELINE_WEBHOOKS_LOADING, } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_PIPELINE_WEBHOOKS: {
            return Object.assign(Object.assign({}, state), { webhooks: action.payload.webhooks });
        }
        case SET_PIPELINE_WEBHOOKS_LOADING: {
            return Object.assign(Object.assign({}, state), { loading: action.payload });
        }
        case ADD_PIPELINE_WEBHOOK: {
            return Object.assign(Object.assign({}, state), { webhooks: [...state.webhooks, action.payload.webhook] });
        }
        case REMOVE_PIPELINE_WEBHOOK: {
            return Object.assign(Object.assign({}, state), { webhooks: state.webhooks.filter((webhook) => webhook.id !== action.payload.webhookId) });
        }
        case EDIT_PIPELINE_WEBHOOK: {
            const { webhookId, webhookData } = action.payload;
            return Object.assign(Object.assign({}, state), { webhooks: state.webhooks.map((webhook) => webhook.id === webhookId ? Object.assign(Object.assign({}, webhook), webhookData) : webhook) });
        }
        default: {
            return state;
        }
    }
};
export { reducer };
