import { SET_STAGE_FILTER, SET_TASK_VIEW_SEARCH_TEXT, SET_USER_FILTER, } from "../constants";
const activePipelineTaskViewReducer = (state, action) => {
    switch (action.type) {
        case SET_USER_FILTER: {
            const { id, type } = action.payload;
            return Object.assign(Object.assign({}, state), { userFilter: {
                    id,
                    type,
                } });
        }
        case SET_STAGE_FILTER: {
            return Object.assign(Object.assign({}, state), { stageIdFilter: action.payload });
        }
        case SET_TASK_VIEW_SEARCH_TEXT: {
            return Object.assign(Object.assign({}, state), { searchText: action.payload });
        }
        default:
            return state;
    }
};
export { activePipelineTaskViewReducer };
